<template>
  <v-card outlined class="slave-apps-grid">
    <v-card-text class="pa-0">
      <v-toolbar dense flat>
        <v-toolbar-title>
          <span>{{ title }}</span>
        </v-toolbar-title>
        <v-spacer></v-spacer>
        <span v-if="latestVersion">
          <span class="grey-text font-weight-medium caption pr-2">Latest Version</span>
          {{ latestVersion }}
        </span>
      </v-toolbar>
      <div class="virtual-scroll-table" v-scroll.self="e => onScroll(e, name)">
        <v-data-table
          dense
          :fixed-header="true"
          hide-default-footer
          :items="virtualDataItems"
          :single-select="false"
          :expanded.sync="expanded"
          :custom-sort="handleCustomSort"
          single-expand
          disable-pagination
          item-key="slaveId"
          :custom-filter="handlGridFilter"
          :search="searchText"
          :headers="headers"
          show-expand
        >
          <template v-slot:header.checkbox="checkboxProps">
            <v-simple-checkbox
              :value="isSelectAll"
              @input="val => handleSelectAll(val, checkboxProps)"
              small
            ></v-simple-checkbox>
          </template>
          <template v-slot:item.checkbox="{ item }">
            <v-simple-checkbox
              v-model="item.isSelected"
              @input="val => handleCheckboxChange(val, item)"
              small
            ></v-simple-checkbox>
          </template>
          <template v-slot:item.slave_site_name="{ item }">
            <span>
              <master-star-indicator :is-master="item.slave_is_master"></master-star-indicator>
              <span>{{ item.slave_site_name }}</span>
            </span>
          </template>
          <template v-if="start > 0" v-slot:body.prepend>
            <tr>
              <td :colspan="headers.length" :style="'padding-top:' + startHeight + 'px'"></td>
            </tr>
          </template>
          <template v-if="start + perPage < this.dataItems.length" v-slot:body.append>
            <tr>
              <td :colspan="headers.length" :style="'padding-top:' + endHeight + 'px'"></td>
            </tr>
          </template>
          <template v-slot:item.state="{ item }">
            <app-status-chip chip-size="small" :state="item.state"></app-status-chip>
          </template>
          <template v-slot:item.is_enabled="{ item }">
            <span class="font-weight-medium">
              <v-chip v-if="item.is_enabled" small dark color="darken-2 green">Enabled</v-chip>
              <v-chip v-if="!item.is_enabled" small dark color="darken-1 brown">Disabled</v-chip>
            </span>
          </template>
          <template v-slot:expanded-item="{ headers, item }">
            <td :colspan="headers.length" class="expanded-table-row py-2">
              <div class="flex pl-15">
                <span class="table-expanded-item">
                  <span class="grey-text font-weight-medium caption">Timezone</span>
                  <span class="text-ellipses">{{ item.slave_time_zone }}</span>
                </span>
                <span class="table-expanded-item">
                  <span class="grey-text font-weight-medium caption">State</span>
                  <span class="font-weight-medium text-ellipses">{{ item.slave_site_state }}</span>
                </span>
                <span class="table-expanded-item">
                  <span class="grey-text font-weight-medium caption">City</span>
                  <span class="font-weight-medium text-ellipses">{{ item.slave_site_city }}</span>
                </span>
                <span class="table-expanded-item">
                  <span class="grey-text font-weight-medium caption">Zip</span>
                  <span class="font-weight-medium text-ellipses">{{ item.slave_site_zip }}</span>
                </span>
              </div>
              <div class="flex pl-15 pt-3 text-left">
                <div class="table-expanded-item">
                  <span class="grey-text font-weight-medium caption">App Features</span>
                  <p>
                    <tooltip-icon-button
                      btnColor="yellow"
                      btnIcon="mdi-upload"
                      btnTooltip="Upload App"
                      :onClick="() => toggleDialog(dialogNames.upload, item)"
                    ></tooltip-icon-button>

                    <tooltip-icon-button
                      btnColor="error"
                      v-if="item.is_installed"
                      btnIcon="mdi-delete"
                      btnTooltip="Uninstall App"
                      :onClick="() => toggleDialog(dialogNames.uninstall, item)"
                    ></tooltip-icon-button>
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn
                          v-bind="attrs"
                          v-on="on"
                          :href="item.browser_download_url"
                          target="_blank"
                          tar
                          v-if="item.browser_download_url"
                          icon
                          small
                        >
                          <v-icon small dark> mdi-zip-box</v-icon>
                        </v-btn>
                      </template>
                      <span>Download ZIP</span>
                    </v-tooltip>
                  </p>
                </div>
                <div class="table-expanded-item">
                  <span class="grey-text font-weight-medium caption">Config File</span>
                  <p>
                    <tooltip-icon-button
                      btnColor="blue"
                      btnIcon="mdi-pencil"
                      btnTooltip="Edit Config File"
                      :onClick="() => toggleDialog(dialogNames.editConfig, item)"
                    ></tooltip-icon-button>
                    <tooltip-icon-button
                      v-if="item.app_type == 'PythonApp' || item.app_type == 'FrontendApp'"
                      btnColor="success"
                      btnIcon="mdi-download"
                      btnTooltip="Download Config File"
                      :onClick="() => downloadConfig(item)"
                    ></tooltip-icon-button>
                    <tooltip-icon-button
                      btnColor="error"
                      btnIcon="mdi-delete"
                      btnTooltip="Delete Configuration"
                      :onClick="() => toggleDialog(dialogNames.deleteAppConfig, item)"
                    ></tooltip-icon-button>
                  </p>
                </div>
                <div class="table-expanded-item">
                  <span class="grey-text font-weight-medium caption">Database</span>
                  <p>
                    <tooltip-icon-button
                      btnColor="success"
                      btnIcon="mdi-download"
                      btnTooltip="Download Database"
                      :onClick="() => onDownloadDatabase(item)"
                    ></tooltip-icon-button>
                    <tooltip-icon-button
                      btnIcon="mdi-delete"
                      btnColor="error"
                      btnTooltip="Delete Database"
                      :onClick="() => toggleDialog(dialogNames.deleteDatabase, item)"
                    ></tooltip-icon-button>
                  </p>
                </div>
                <div class="table-expanded-item">
                  <span v-if="item.is_installed" class="grey-text text-left font-weight-medium caption">Actions</span>
                  <p v-if="item.is_installed">
                    <tooltip-icon-button
                      btnColor="blue darken-2"
                      btnIcon="mdi-play-circle-outline"
                      btnTooltip="Start Service"
                      :onClick="() => controlSlaveApp(item, controlActions.START)"
                    ></tooltip-icon-button>
                    <tooltip-icon-button
                      btnColor="success"
                      btnIcon="mdi-restart"
                      btnTooltip="Restart Service"
                      :onClick="() => toggleDialog(dialogNames.restartService, item)"
                    ></tooltip-icon-button>
                    <tooltip-icon-button
                      btnColor="white"
                      btnIcon="mdi-stop-circle-outline"
                      btnTooltip="Stop Service"
                      :onClick="() => controlSlaveApp(item, controlActions.STOP)"
                    ></tooltip-icon-button>
                    <tooltip-icon-button
                      btnColor="teal darken-2"
                      btnIcon="mdi-check-circle-outline"
                      btnTooltip="Enable Service"
                      :onClick="() => controlSlaveApp(item, controlActions.ENABLE)"
                    ></tooltip-icon-button>
                    <tooltip-icon-button
                      btnColor="orange darken-2"
                      btnIcon="mdi-close-octagon"
                      btnTooltip="Disable Service"
                      :onClick="() => controlSlaveApp(item, controlActions.DISABLE)"
                    ></tooltip-icon-button>
                  </p>
                </div>
              </div>
            </td>
          </template>
        </v-data-table>
      </div>
    </v-card-text>
    <confirm-dialog
      :is-open="dialog[dialogNames.deleteDatabase]"
      :on-close="() => closeDialog(dialogNames.deleteDatabase)"
      :on-confirm="() => onDeleteDatabase()"
      :loading="isDeletingDatabase"
      dialog-text="Do you want to delete the app's database?"
    ></confirm-dialog>

    <confirm-dialog
      :is-open="dialog[dialogNames.restartService]"
      :on-close="() => closeDialog(dialogNames.restartService)"
      :on-confirm="() => restartService()"
      :loading="isRestartingService"
      dialog-text="Are you sure to restart the app?"
    ></confirm-dialog>

    <confirm-dialog
      :is-open="dialog[dialogNames.deleteAppConfig]"
      :on-close="() => closeDialog(dialogNames.deleteAppConfig)"
      :on-confirm="() => deleteAppConfigFile()"
      :loading="isDeletingConfig"
      dialog-text="Do you want to delete the app's config file?"
    ></confirm-dialog>

    <v-dialog presistent v-model="dialog[dialogNames.editConfig]" max-width="500px">
      <apps-edit-config
        v-if="dialog[dialogNames.editConfig]"
        :is-slave-app="true"
        :close-dialog="syncAppDetails => closeDialog(dialogNames.editConfig, syncAppDetails)"
        :app-detail="selectedApp"
      ></apps-edit-config>
    </v-dialog>

    <confirm-dialog
      :is-open="dialog[dialogNames.uninstall]"
      :on-close="() => closeDialog(dialogNames.uninstall)"
      :on-confirm="() => uninstall(dialogNames.uninstall)"
      :loading="isUninstallingApp"
      dialog-text="Do you want to uninstall the app?"
    ></confirm-dialog>

    <v-dialog persistent v-model="dialog[dialogNames.upload]" max-width="500px">
      <apps-upload-multi-step-view
        v-if="dialog[dialogNames.upload]"
        :app-details="selectedApp"
        :is-slave-app="true"
        :close-dialog="syncAppDetails => closeDialog(dialogNames.upload, syncAppDetails)"
      ></apps-upload-multi-step-view>
    </v-dialog>
  </v-card>
</template>
<script>
import JsZip from 'jszip';
import GridToolbar from '../Common/GridToolbar';
import ConfirmDialog from '../Common/ConfirmDialog';
import AppStatusChip from '../Services/AppStatusChip';
import AppsEditConfig from '../RubixApps/appsEditConfig';
import TooltipIconButton from './TooltipIconButton.vue';
import TextFieldDisplay from '../Common/TextFieldDisplay.vue';
import * as slaveAppsService from '../../services/slaveAppsService';
import MasterStarIndicator from '../Common/MasterStarIndicator.vue';
import AppsUploadMultiStepView from '../RubixApps/AppsUploadMultiStepView';
import { RUBIX_APP_STATUS_CONTROL_ACTIONS } from '../../constants/appConstant';

const MAX_VISIBLE_GRID_ITEMS = 10;
export default {
  name: 'slave-app-grid',
  props: [
    'slaveApps',
    'selectedItems',
    'latestVersion',
    'slaveDevices',
    'onSelectItem',
    'searchText',
    'filters',
    'name',
    'slaveAppsDataItems',
  ],
  components: {
    AppStatusChip,
    GridToolbar,
    TextFieldDisplay,
    TooltipIconButton,
    ConfirmDialog,
    AppsEditConfig,
    MasterStarIndicator,
    AppsUploadMultiStepView,
  },
  data() {
    const dialogNames = {
      editConfig: 'editConfig',
      restartService: 'restartService',
      deleteDatabase: 'deleteDatabase',
      deleteAppConfig: 'deleteAppConfig',
      upload: 'upload',
      uninstall: 'uninstall',
    };
    return {
      expanded: [],
      isDeletingConfig: false,
      isRestartingService: false,
      isUninstallingApp: false,
      isDeletingDatabase: false,
      controlActions: { ...RUBIX_APP_STATUS_CONTROL_ACTIONS },
      dialog: {
        [dialogNames.deleteDatabase]: false,
        [dialogNames.restartService]: false,
        [dialogNames.upload]: false,
        [dialogNames.editConfig]: false,
        [dialogNames.deleteAppConfig]: false,
        [dialogNames.uninstall]: false,
      },
      dialogNames,
      selectedGridItems: [],
      selectedApp: {},
      start: 0,
      timeout: null,
      rowHeight: 33,
      perPage: 25,
      headers: [
        {
          text: '',
          align: 'left',
          value: 'checkbox',
          sortable: false,
        },
        {
          text: 'Site Name',
          align: 'left',
          value: 'slave_site_name',
        },
        {
          text: 'Device Name',
          align: 'left',
          value: 'slave_device_name',
        },
        {
          text: 'Status',
          align: 'right',
          value: 'state',
        },
        {
          text: 'Installed Version',
          align: 'center',
          sortable: true,
          value: 'version',
        },
        {
          text: 'Active Since',
          align: 'center',
          sortable: true,
          value: 'time_since',
        },
        {
          text: 'State',
          align: 'center',
          value: 'is_enabled',
        },
        { text: '', value: 'data-table-expand' },
      ],
      dataItems: [],
    };
  },
  mounted() {
    this.dataItems = this.slaveAppsDataItems
      .filter(val => !(this.filters.isInstalled && !val.is_installed))
      .map(data => this.appendSlaveDevice(data));
  },
  computed: {
    setting() {
      return this.$store.state.setting;
    },
    title() {
      return this.slaveApps.display_name;
    },
    serviceName() {
      return this.slaveApps.name;
    },
    virtualDataItems() {
      return this.dataItems.slice(this.start, this.perPage + this.start);
    },
    startHeight() {
      return this.start * this.rowHeight - 33;
    },
    endHeight() {
      return this.rowHeight * (this.dataItems.length - this.start);
    },
    isFixedHeader() {
      return this.dataItems.length > MAX_VISIBLE_GRID_ITEMS;
    },
    isSelectAll() {
      return this.selectedGridItems.length == this.dataItems.length;
    },
  },
  methods: {
    handleSelectAll(val, checkboxProps) {
      this.dataItems = this.dataItems.map(item => ({ ...item, isSelected: val }));
      if (val) {
        this.selectedGridItems = this.dataItems;
      } else {
        this.selectedGridItems = [];
      }
      this.handleOnGridItemSelect();
    },
    handleOnGridItemSelect() {
      this.onSelectItem(this.serviceName, this.selectedGridItems);
    },
    onScroll(e, name) {
      if (name == this.name) {
        // debounce if scrolling fast
        this.timeout && clearTimeout(this.timeout);

        this.timeout = setTimeout(() => {
          const { scrollTop } = e.target;
          const rows = Math.ceil(scrollTop / this.rowHeight);

          this.start = rows + this.perPage > this.dataItems.length ? this.dataItems.length - this.perPage : rows;
          this.$nextTick(() => {
            e.target.scrollTop = scrollTop;
          });
        }, 20);
      }
    },
    appendSlaveDevice(data) {
      let newData = { ...data };
      const slaveDevice = this.slaveDevices[data.slaveId];
      if (slaveDevice) {
        newData = {
          isExpanded: false,
          isSelected: false,
          ...newData,
          time_since: newData.time_since ? newData.time_since : '-',
          ...Object.keys(slaveDevice).reduce((acc, curr) => {
            const key = 'slave_' + curr;
            if (key === 'slave_site_name' && slaveDevice.is_master) {
              acc[key] = `   ${slaveDevice[curr]}`; // Three spaces to identify as master device
              return acc;
            }
            acc[key] = slaveDevice[curr];
            return acc;
          }, {}),
        };
      }
      return newData;
    },
    handleCheckboxChange(val, item) {
      if (!item) {
        return;
      }

      const isItemSelected = this.selectedGridItems.find(gridItem => gridItem.slaveId === item.slaveId);

      if (!isItemSelected && val) {
        this.selectedGridItems.push(item);
      }

      if (isItemSelected && !val) {
        this.selectedGridItems = this.selectedGridItems.filter(gridItem => gridItem.slaveId !== item.slaveId);
      }
      this.handleOnGridItemSelect();
    },
    toggleItemExpansion(item) {
      item.isExpanded = !item.isExpanded;
    },
    toggleItemSelection(item) {
      item.isSelected = !item.isSelected;
    },
    closeDialog(dialogName, syncAppDetails) {
      this.dialog[dialogName] = false;
      this.selectedApp = {};
      syncAppDetails && typeof this.fetchAppDetails === 'function' && this.fetchAppDetails(true);
    },
    toggleDialog(dialogName, item) {
      this.dialog[dialogName] = !this.dialog[dialogName];

      if (this.dialog[dialogName]) {
        this.selectedApp = item;
      } else {
        this.selectedApp = null;
      }
    },
    onDeleteDatabase() {
      this.isDeletingDatabase = true;
      const serviceName = this.selectedApp.service;
      const slaveId = this.selectedApp.slaveId;
      slaveAppsService
        .deleteAppData(serviceName, slaveId)
        .then(() => {
          this.$toasted.show(`Successfully deleted app data for ${serviceName}`);
          this.closeDialog(this.dialogNames.deleteDatabase, true);
        })
        .catch(() => {
          this.closeDialog(this.dialogNames.deleteDatabase);
        })
        .finally(() => {
          this.isDeletingDatabase = false;
        });
    },
    downloadConfig(appDetail) {
      const serviceName = appDetail.service;
      const appType = appDetail.app_type;
      const slaveId = appDetail.slaveId;
      const slaveClientName = appDetail.slave_device_name;
      slaveAppsService.downloadAppConfig(serviceName, slaveId, slaveClientName, appType);
    },
    deleteAppConfigFile() {
      this.isDeletingConfig = true;
      const serviceName = this.selectedApp.service;
      const appType = this.selectedApp.app_type;
      const slaveId = this.selectedApp.slaveId;

      slaveAppsService
        .deleteAppConfig(slaveId, serviceName, appType)
        .then(data => {
          this.$toasted.show(`${serviceName} app config file is deleted`);
          this.closeDialog(this.dialogNames.deleteAppConfig);
        })
        .catch(err => {
          this.$toasted.show(`Failed to delete app config for ${serviceName}`, { type: 'error' });
        })
        .finally(() => {
          this.isDeletingConfig = false;
        });
    },
    onDownloadDatabase(item) {
      const serviceName = item.service;
      const slaveId = item.slaveId;
      slaveAppsService
        .downloadAppData(serviceName, slaveId, this.setting)
        .then(async blobData => {
          const zip = new JsZip();
          await zip.loadAsync(blobData);
          const blob = await zip.generateAsync({ type: 'blob' });
          const date = new Date();
          const filename = this.getFileName(serviceName);
          const link = document.createElement('a');
          link.href = window.URL.createObjectURL(blob);
          link.setAttribute('download', `${filename}.zip`);
          link.click();
          this.$toasted.show(`${serviceName} app data is exported.`);
        })
        .catch(err => {
          const reader = new FileReader();
          reader.addEventListener('loadend', e => {
            const err = JSON.parse(e.target.result);
            if (err && err.message) {
              this.$toasted.show(err.message, { type: 'error' });
            } else {
              this.$toasted.show(`Failed to export ${serviceName} app data`, { type: 'error' });
            }
          });
          reader.readAsText(err);

          return err;
        });
    },
    uninstall() {
      this.isUninstallingApp = true;
      const serviceName = this.selectedApp.service;
      const slaveId = this.selectedApp.slaveId;

      slaveAppsService
        .uninstallApp(slaveId, serviceName)
        .then(() => {
          this.$toasted.show('Successfully unistalled app');
          this.closeDialog(this.dialogNames.uninstall, true);
        })
        .catch(() => {
          this.closeDialog(this.dialogNames.uninstall);
        })
        .finally(() => {
          this.isUninstallingApp = false;
        });
    },
    restartService() {
      this.isRestartingService = true;
      const service = this.selectedApp.service;
      const slaveId = this.selectedApp.slaveId;
      slaveAppsService
        .changeServiceStatus(RUBIX_APP_STATUS_CONTROL_ACTIONS.RESTART, service, slaveId)
        .then(() => {
          this.closeDialog(this.dialogNames.restartService, true);
          this.$toasted.show(`App restarted sucessfully.`);
        })
        .catch(err => {
          this.$toasted.show(`Failed to restart the app.`, { type: 'error' });
        })
        .finally(() => {
          this.isRestartingService = false;
        });
    },
    controlSlaveApp(selectedApp, action) {
      const service = selectedApp.service;
      const slaveId = selectedApp.slaveId;
      return slaveAppsService
        .changeServiceStatus(action, service, slaveId)
        .then(() => {
          this.$toasted.show(`Sucessfully performed ${action} to selected app.`);
        })
        .catch(err => {
          this.$toasted.show(`Failed to ${action} the app.`, { type: 'error' });
        });
    },
    handleCustomSort(items, sortByItems, sortDescItems) {
      // ignore virtualized items and sort in the real items and set the virtualized items again
      let dataItems = this.dataItems
        .filter(val => !(this.filters.isInstalled && !val.is_installed))
        .map(data => this.appendSlaveDevice(data));
      const sortBy = (Array.isArray(sortByItems) && sortByItems.length > 0 && sortByItems[0]) || null;
      const sortDesc = Array.isArray(sortDescItems) && sortDescItems.length > 0 && sortDescItems[0];
      if (sortBy) {
        dataItems = dataItems.sort((a, b) => compare(a, b, sortBy));
        if (sortDesc) {
          dataItems = dataItems.reverse();
        }
      }
      return dataItems.slice(this.start, this.perPage + this.start);

      function compare(a, b, key) {
        if (a[key] < b[key]) {
          return -1;
        }
        if (a[key] > b[key]) {
          return 1;
        }
        return 0;
      }
    },
    handlGridFilter(value, search, item) {
      return value != null && search != null && typeof value === 'string' && value.toString().indexOf(search) !== -1;
    },
  },
};
</script>
<style>
